@font-face {
  font-family: "Fira Sans";
  font-display: swap;
  font-style: normal;
  src: local("FiraSansRegular"), local("FiraSans-Regular"),
  url("../fonts/FiraSans-Regular.woff2") format("woff2"),
  url("../fonts/FiraSans-Regular.woff") format("woff");
  font-weight: 400;
}

